.meal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
}
.image {
  width: 330px;
  height: 350px;
  object-fit: cover;
}

.meal h3 {
  margin: 0 0 0.25rem 0;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: #ad5502;
  font-size: 1.25rem;
}
